@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}


html {
  box-sizing: border-box;
}


/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,700;0,900;1,600;1,900&display=swap'); */


.App {
  min-height: 100vh;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding-left: 2rem;
  padding-right: 2rem;
  color: #fff;
}


.nav-container {
    padding: 0 100px;
    width: 100%;
    height: 100%;
    min-height: 110px;
    max-width: 1500px;
    display: flex;
    align-items: center;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    background: #F1F7FD;
}

.nav {
    display: flex;
    text-align: left;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    background: transparent;
    width: 100%;
}

.nav-items {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.nav-items a {
    text-decoration: none;
    margin: 8px 24px;
    color: #2D323B;
}

.nav-items button {
    padding: 12px 30px;
    background-color: #41C2D2;
    color: #fff;
    border: none;
    box-shadow: 0px 4px 0px #31919D;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
}

.nav-items button img {
    width: 12px;
    margin-left: 8px;
}
.footer-background {
    width: 100vw;
    background-color: #F1F7FD;
}

.footer-container {
    width: 100%;
    /* min-height: 200px; */
    max-width: 800px;
    /* padding: 0 100px; */
    margin:auto;
    text-align: center;
}

.form-field {
    display: grid;
    grid-template-columns: 2fr 2fr 1fr;
    grid-gap: 1rem;
    padding-bottom: 4rem;
}

.three {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background-color: #fff;
    padding: 15px 10px;
    box-shadow: 0px -4px 0px #CFCFCF;
    border-radius: 5px;
    font-size: .8rem;
    color: #555555;
}

#footer button {
    padding: 12px 30px;
    background-color: #41C2D2;
    color: #fff;
    border: none;
    box-shadow: 0px 4px 0px #31919D;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
}

#footer h2 {
    margin: 2rem 0rem;
}

#void {
    width: 100vw;
    min-height: 100vh;
    background-color: #000;
    text-align: center;
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    justify-content: center;
}

#void img {
    width: 400px;
    text-align: center;
}

